@import "./common.scss";

.container {
  @include flex(row, stretch);
  width: 100%;
  height: 100%;
}

.sider {
  @include flex(column, stretch);
  flex-basis: 40px;
  position: relative;
  background-color: rgb(36, 38, 47);
  box-shadow: rgba(0, 21, 41, 0.12) 2px 0px 6px 0px;
  transition: all 0.35s ease-in-out;

  &.unflod {
    flex-basis: 180px;
  }
}

.menus {
  @include flex(column);

  ul {
    padding: 0;
    margin: 0;

    li {
      @include flex(row, center, center);
      width: 40px;
      height: 40px;
      box-sizing: border-box;
      border: 1px solid #24262f;
      transition: all 0.3s ease-in-out;

      &:hover {
        border-color: #e8e8e8;
      }
    }
  }
}

.flod {
  @include flex(column, flex-start, center);
  height: 40px;
  padding-left: 9px;

  &:hover {
    background-color: #ededed;
  }
}

.content {
  @include flex(column, stretch);
  flex: 1;
  background-color: rgb(248, 248, 254);
}

.tool {
  @include flex(row, center, flex-end);
  flex-basis: 40px;
  background-color: rgb(36, 38, 47);
  box-shadow: rgba(0, 21, 41, 0.12) 2px 0px 6px 0px;
  border-bottom: 1px solid #0d0e10;

  span {
    @include flex(row, center, center);
    width: 40px;
    height: 40px;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
      background-color: rgba(0, 0, 0, 0.2);
    }
  }
}

.edit {
  flex: 1;
  position: relative;
  border-radius: 2px;
  background-color: #fff;
  overflow: hidden;
}
