@import "../../assets/scss/common.scss";

.ruler {
  height: 100%;
  background-color: rgb(24, 26, 35);
  position: relative;
}

.aided {
  @include flex(row, center, center);
  top: 0;
  left: 0;
  width: 23px;
  height: 20px;
  position: absolute;
}

.abscissa {
  top: 0;
  left: 20px;
  height: 20px;
  width: 6000px;
  position: absolute;
  background-color: rgb(21, 22, 31);
}

.ordinate {
  top: 20px;
  left: 0;
  width: 20px;
  height: 6000px;
  position: absolute;
  background-color: rgb(21, 22, 31);
}

.container {
  position: absolute;
  top: 20px;
  left: 20px;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(
      45deg,
      #404040 25%,
      transparent 0,
      transparent 75%,
      #404040 0
    ),
    linear-gradient(
      45deg,
      #404040 25%,
      transparent 0,
      transparent 75%,
      #404040 0
    );
  background-color: #4a4a4a;
  background-size: 10px 10px;
  background-position: 0 0, 5px 5px;
}

.backdrop {
  width: 960px;
  height: 540px;
  transform-origin: 0 0;
  background-color: rgb(35, 38, 48);
  position: relative;
}

.item {
  @include flex(row, center, center);
  color: #fff;
  font-size: 10px;
  border: 1px solid #fff;
  position: absolute;
  box-sizing: border-box;
}

.box {
  top: 60px;
  left: 60px;
  position: absolute;
  background: #ccc;
  border: 1px solid black;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  overflow: hidden;
}

.box .text {
  text-align: center;
}

.guides {
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  position: absolute;
  pointer-events: none;
}

.line {
  @include flex(row, flex-end, flex-end);
  position: absolute;
  top: 0;
  left: 0;
  border: 1px dashed #808080;
  border-top: none;
  border-left: none;
  color: #fff;
  text-align: right;
  z-index: 999;

  span {
    color: #808080;
    font-size: 12px;
    line-height: 30px;
    padding-right: 10px;
  }
}

.spacing {
  @include flex(row, center, center);
  position: absolute;
  background-color: rgb(236, 98, 43);
  z-index: 1000;

  span {
    color: #808080;
    font-size: 12px;
    line-height: 20px;
    position: absolute;
  }

  &::after,
  &::before {
    content: "";
    width: 1px;
    height: 8px;
    background-color: rgb(236, 98, 43);
    position: absolute;
  }

  &.vertical {
    &::after {
      top: -4px;
      transform: rotate(55deg);
    }

    &::before {
      bottom: -4px;
      transform: rotate(55deg);
    }
  }

  &.horizon {
    &::after {
      left: -1px;
      transform: rotate(25deg);
    }

    &::before {
      right: -1px;
      transform: rotate(25deg);
    }
  }
}
